import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    year: null,
    title: ''
  },
  mutations: {
    SET_YEAR(state, { year }) {
      state.year = year;
    },
    SET_TITLE(state, title) {
      state.title = title;
    }
  },
  actions: {},
  modules: {}
});
